import React, { Component } from 'react'
import { ReactComponent as IconLetter } from '../images/icon_letter.svg';

export default class Footer extends Component {
  render() {
    return (
      <section className="newsletter">
        <h3><IconLetter /> Sign Up &amp; Stay Connected</h3>
        <div className="newsletter__content">
          <p>Sign up for the newsletter and get 20% off! Gain access to exclusive offers and be the first to know when new stuff drops!</p>
          <form>
            <label className="visually-hidden" htmlFor="newsletter-signup">Email Address</label>
            <input id="newsletter-signup" type="email" placeholder="Enter Your Email Address"></input>
            <button className="btn btn--secondary">Subscribe</button>
          </form>
        </div>
      </section>
    );
  }
}
