import React, { Component } from 'react'

import { ReactComponent as IconClose } from '../images/icon_close.svg';
import { ReactComponent as Arrow } from '../images/arrow.svg';
import footerImg from '../images/social1.png';

export default class Menu extends Component {

  componentDidMount(){
    const menuContainer = document.querySelector('.menu__container');
    const menuOpen = document.querySelector('.nav__hamburger');
    const menuClose = document.querySelector('.close');
    let menuTabs = document.querySelectorAll('.state--arrow');

    menuOpen.addEventListener('click', (e) => {
      e.preventDefault();
      document.body.classList.add('no-scroll');
      menuContainer.classList.add('menu__container--show');
    });

    menuClose.addEventListener('click', (e) => {
      e.preventDefault();
      menuContainer.classList.remove('menu__container--show');
      document.body.classList.remove('no-scroll');
    });

    //add event listeners
    for(let tab of menuTabs) {
      tab.addEventListener('click', (e) => {
        e.preventDefault();
        tab.classList.toggle('state--open');
      })
    }
  }

  render() {
    return (

      <div className="menu__container">
        <div className="menu">
          <div className="menu__header">
            <span className="close">
              <IconClose />
            </span>
          </div>
          <div className="menu__body">
          <ul>
            <li className="state--arrow state--open">Tops<span className="icon icon--open"><Arrow /></span><span className="icon icon--close"><Arrow /></span></li>
              <ul>
                <li>
                  <a href="/" title="">
                    T-Shirts
                  </a>
                </li>
                <li>
                  <a href="/" title="">
                    Short Sleeve
                  </a>
                </li>
                <li>
                  <a href="/" title="">
                    Long Sleeve
                  </a>
                </li>
              </ul>
          </ul>
          <ul>
            <li className="state--arrow state--open">Bottoms<span className="icon icon--open"><Arrow /></span><span className="icon icon--close"><Arrow /></span></li>
              <ul>
              <li>
                <a href="/" title="">
                  Shorts
                </a>
              </li>
              <li>
                <a href="/" title="">
                  Pants
                </a>
              </li>
              </ul>
          </ul>
          </div>
          <div className="menu__footer">
            <div className="img__container">
              <img alt="Man wearing short sleeve shirt" src={footerImg}/>
            </div>
            <ul>
              <li>
                <a href="/" title="">
                  Shop All
                </a>
              </li>
              <li>
                <a href="/" title="">
                  New Arrivals
                </a>
              </li>
              <li>
                <a href="/" title="">
                  Favorites
                </a>
              </li>
              <li>
                <a href="/" title="">
                  Sale
                </a>
              </li>
            </ul>
          </div>
        </div>
      </div>
    );
  }
}
