import React, { Component } from 'react'
import product from '../images/product.png';


// Import Swiper React components
import Swiper, { Navigation } from 'swiper';

// swiper bundle styles
import 'swiper/swiper-bundle.min.css'

// swiper core styles
import 'swiper/swiper.min.css'

// modules styles
import 'swiper/components/navigation/navigation.min.css'

Swiper.use([Navigation]);

export default class CarouselProduct extends Component {
  componentDidMount(){
    this.swiper=new Swiper('.swiper-container--product',{
        loop: true,
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev",
        },
        slidesPerGroup: 2,
        // Responsive breakpoints
        breakpoints: {
          // when window width is >= 320px
          1: {
            slidesPerView: 2,
            spaceBetween: 10,
          },
          // when window width is >= 480px
          769: {
            slidesPerView: 4,
            spaceBetween: 20,
          },
        },
      })
  }
  render() {
    return (
      <section className="carousel__product">
        <h3>Shop New Arrivals</h3>
        <div className="swiper-container swiper-container--product">
          <div className="swiper-wrapper">
            <div className="swiper-slide">
              <a href="/" title="Link to Product Title">
                <img src={product} alt="New Arrivals Hero" />
                <div className="slide__content">
                  <h4>Product Title</h4>
                  <p>Women's T-shirt</p>
                  <p>$19.99</p>
                </div>
              </a>
            </div>
            <div className="swiper-slide">
              <a href="/" title="Link to Product Title">
                <img src={product} alt="New Arrivals Hero" />
                <div className="slide__content">
                  <h4>Product Title</h4>
                  <p>Women's T-shirt</p>
                  <p>$19.99</p>
                </div>
              </a>
            </div>
            <div className="swiper-slide">
              <a href="/" title="Link to Product Title">
                <img src={product} alt="New Arrivals Hero" />
                <div className="slide__content">
                  <h4>Product Title</h4>
                  <p>Women's T-shirt</p>
                  <p>$19.99</p>
                </div>
              </a>
            </div>
            <div className="swiper-slide">
              <a href="/" title="Link to Product Title">
                <img src={product} alt="New Arrivals Hero" />
                <div className="slide__content">
                  <h4>Product Title</h4>
                  <p>Women's T-shirt</p>
                  <p>$19.99</p>
                </div>
              </a>
            </div>
          </div>
          <div className="swiper-button-next"></div>
          <div className="swiper-button-prev"></div>
        </div>
      </section>
    );
  }
}
