import React, { Component } from 'react'
import hero from '../images/hero.png';
import heroDesk from '../images/hero_desktop.png';

export default class Hero extends Component {
  render() {
    return (
      <section className="home__hero">
        <picture>
          <source media="(min-width:768px)" srcSet={heroDesk} />
          <img src={hero} alt="Favorite Tees Banner" />
        </picture>
        <div className="hero__content">
          <h2>Our Favorite Tees</h2>
          <p>Everyday tees you need!</p>
          <button className="btn btn--small">Shop Now</button>
        </div>
      </section>
    );
  }
}
