import React, { Component } from 'react'
import collection1 from '../images/collection_womens.png';
import collection2 from '../images/collection_mens.png';
import collection3 from '../images/collection_accessories.png';
import collection3Desk from '../images/collection_accessories_desktop.png';

export default class Collection extends Component {
  render() {
    return (
      <section className="home__collection">
        <div className="collection__card">
          <picture>
            <img src={collection1} alt="Womens Collection" />
          </picture>
          <span>Womens</span>
        </div>
        <div className="collection__card">
          <picture>
            <img src={collection2} alt="Mens Collection" />
          </picture>
          <span>Mens</span>
        </div>
        <div className="collection__card">
          <picture>
            <source media="(min-width:768px)" srcSet={collection3Desk} />
            <img src={collection3} alt="Accessories Collection" />
          </picture>
          <span>Accessories</span>
        </div>
      </section>
    );
  }
}
