import React, { Component } from 'react'
import { ReactComponent as IconFacebook } from '../images/icon_facebook.svg';
import { ReactComponent as IconInstagram } from '../images/icon_instagram.svg';
import { ReactComponent as IconTwitter } from '../images/icon_twitter.svg';

export default class Footer extends Component {

  componentDidMount(){
    let footerTabs = document.querySelectorAll('.state');

    //activate first tab
    const firstTab = footerTabs[0];
    firstTab.classList.add('state--open');

    //add event listeners
    for(let tab of footerTabs) {
      tab.addEventListener('click', (e) => {
        e.preventDefault();
        tab.classList.toggle('state--open');
      })
    }
  }

  render() {
    return (
      <footer>
        <nav className="footer__nav">
          <ul>
            <li className="state">Customer Service<span className="icon icon--open">+</span><span className="icon icon--close">-</span></li>
              <ul>
                <li>Accessibility</li>
                <li>Contact Us</li>
                <li>Return Policy</li>
                <li>FAQ</li>
                <li>Gift Certificates</li>
                <li>Wishlist</li>
              </ul>
          </ul>
          <ul>
            <li className="state">Company<span className="icon icon--open">+</span><span className="icon icon--close">-</span></li>
              <ul>
                <li>About Us</li>
                <li>Careers</li>
                <li>Press</li>
                <li>Affiliates</li>
              </ul>
          </ul>
        </nav>
        <div className="footer__social">
          <h3>Follow Us</h3>
          <ul>
            <li>
              <a href="/" title="Link to Facebook">
                <IconFacebook />
              </a>
            </li>
            <li>
              <a href="/" title="Link to Instagram">
                <IconInstagram />
              </a>
            </li>
            <li>
              <a href="/" title="Link to Twitter">
                <IconTwitter />
              </a>
            </li>
          </ul>
        </div>
      </footer>
    );
  }
}
