import React, { Component } from 'react'
import hero1 from '../images/slider1.png';
import hero2 from '../images/slider2.png';
import hero3 from '../images/slider3.png';

// Import Swiper React components
import Swiper, { Pagination } from 'swiper';

// swiper bundle styles
import 'swiper/swiper-bundle.min.css'

// swiper core styles
import 'swiper/swiper.min.css'

// modules styles
import 'swiper/components/pagination/pagination.min.css'

Swiper.use([Pagination]);

export default class CarouselHero extends Component {
  componentDidMount(){
    this.swiper=new Swiper('.swiper-container--hero',{
        loop: true,
        pagination: {
          el: ".swiper-pagination",
        },
      })
  }
  render() {
    return (
      <section className="carousel__hero">
        <div className="swiper-container swiper-container--hero">
          <div className="swiper-wrapper">
            <div className="swiper-slide">
              <img src={hero1} alt="New Arrivals Hero" />
              <div className="slide__content">
                <h2>Shop New Arrivals</h2>
                <p>Our coolest new items are waiting for you!</p>
                <button className="btn btn--small">Shop Now</button>
              </div>
            </div>
            <div className="swiper-slide">
              <img src={hero2} alt="Fave Tees Hero" />
              <div className="slide__content">
                <h2 className="text-white--desktop">Our Fave Tees</h2>
                <p className="text-white--desktop">Shop all of our favorites.</p>
                <button className="btn btn--small">Shop Now</button>
              </div>
            </div>
            <div className="swiper-slide">
              <img src={hero3} alt="Mens Tees Hero" />
              <div className="slide__content">
                <h2 className="text-white--desktop">Men's Tees</h2>
                <p className="text-white--desktop">Find the perfect shirt.</p>
                <button className="btn btn--small">Shop Now</button>
              </div>
            </div>
          </div>
          <div className="swiper-pagination"></div>
        </div>
      </section>
    );
  }
}
